*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    
  }
  main{
    background-image: url(/TNrover-/static/media/background.a8de7b68.jpg);
    background-size: cover;
    padding-top: 2%;
    padding-bottom: 3%;
  }
  .App{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
  }
  .App-register{
    width: 35%;
    min-height: 500px;
    margin: 6% auto;
    background-color: darkblue;
    color: white;
    font-size: 1.5rem;
    padding: 1rem;
    border-radius: 10px;
  display: flex;
  flex-direction: column;}
  .App-register h1{
    margin: 10px auto;
    text-align: center;
  }
  .App-register form{
    background-color: darkblue;
    padding: 1rem;
    color:white;
    display: flex;
    flex-direction: column;
    grid-row-gap: 1rem;
    row-gap: 1rem;
    border-radius: 10px;
  }
  .App-register form label{
    align-self: center;
    font-weight: bold;
    font-size: 1.3rem;
  }
  .App-register input{
    width: 60%;
    height: 35px;
    justify-self: center;
    align-self: center;
    border-radius: 4px;
    border: none;
    font-size: 1rem;
    padding: 0.2rem;
    color:darkblue;
    font-weight: bolder;
  }
  .App-register input:focus{
    border: 2px solid yellow;
    outline:2px solid yellow;
  }
  .pwd-note{
    background-color: black;
    color:white;
    font-size: 0.9rem;
    width: 80%;
    align-self: center;
    padding: 0.5rem;
    border-radius: 5px;
  }
  .App-register .auth-btn{
    width: 50%;
    align-self: center;
    border: none;
    height: 40px;
    margin-top: 1rem;
    margin: 1rem auto;
    font-size: 1.2rem;
    color: darkblue;
    border-radius: 10px;
    cursor: pointer;
    font-weight: bolder;
  }
  .App-register .auth-btn:hover{
    opacity: 0.7;
  }
  .already{
    font-size: 1.2rem;
    align-self: center;
  }
  .sign-in{
    color:white;
    font-size: 1.2rem;
    text-decoration: underline;
    align-self: center;
    margin: -18px auto;
  }
  .sign-in:hover{
    color:white;
    opacity: 0.7;
  }
  .sign-up:hover{
    color: white;
    opacity: 0.7;
  }
  .err-msg{
    background-color: hsla(0, 60%, 71%, 0.5);
    color:red;
    width: 80%;
    margin: 0px auto;
    padding:0.5rem;
    border-radius: 9px;
    text-align: center;
  }
  @media  screen and (max-width: 800px) {
    main{
      padding-bottom: 30%;
      margin: auto 0;
    }
    .App-register{
      width: 90%;
      
    }
    .App-register input{
      width: 94%;
    }
    .pwd-note{
      width: 94%;
    }
    .err-msg{
      width: 94%;
      font-size: 0.9rem;
      font-weight: bold;
    }
  }
body {
  background: #f7f7f7 !important;
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: "Fira Sans", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
label,
span {
  font-weight: 500;
  font-family: "Fira Sans", sans-serif;
}

body,
html,
.App,
#root,
.auth-wrapper {
  width: 100%;
  height: 100%;
}

.navbar-light {
  background-color: #f7f7f7;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}

.auth-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
}

.auth-inner {
  width: 450px;
  margin: auto;
  background: #f7f7f7;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all 0.3s;
}

.auth-wrapper .form-control:focus {
  border-color: #fcfdfd;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
